.wio-link {
  position: fixed;
  bottom: 30px;
  right: 30px;

  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  border-radius: 24px;
  background-color: #fafafa;
  box-shadow: 0 2px 7px 0 rgba(90, 90, 140, 0.40);
}
html,body {
  background: #EFF3F4;
  font-family: 'Open Sans', serif;
}

.hero.is-info.is-bold {
  background: #ebe8e6;
}
@media screen and (min-width: 769px) {
  .hero.is-info.is-bold {
    background: url('//c1.staticflickr.com/1/826/42132264271_6bd7de4f83_k.jpg')center center; background-size:cover;
  }
  .hero.is-medium .hero-body {
    padding-bottom: 16rem;
    padding-top: 9rem;
  }
}
.navbar-item img {
  max-height: 4rem;
}
.hero-body .container {
  max-width: 700px;
  margin-left: 2rem;
}
.hero-body .title {
  color: #2f2b27 !important;
  text-shadow: 1px 1px #ebe8e6;
}
.hero-body .subtitle {
  color: #453e38 !important;
  text-shadow: 1px 1px #ebe8e6;
  padding-top: 0.5rem;
  padding-bottom: 1.5rem;
  line-height: 1.5;
}
.features {
  padding: 5rem 0;
}
.box.cta {
  border-radius: 0;
  border-left: none;
  border-right: none;
}
.card-image > .fa {
  font-size: 8rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
  color: #209cee;
}
.card-content .content {
  font-size: 14px;
  margin: 1rem 1rem;
}
.card-content .content h4 {
  font-size: 16px;
  font-weight: 700;
}
.card {
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.18);
  margin-bottom: 2rem;
}
.intro {
  padding: 3rem 0;
  text-align: center;
}
.sandbox {
  padding: 2rem 0;
}
.tile.notification {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.is-shady {
  animation: flyintoright .4s backwards;
  background: #fff;
  box-shadow: rgba(0, 0, 0, .1) 0 1px 0;
  border-radius: 4px;
  display: inline-block;
  margin: 10px;
  position: relative;
  transition: all .2s ease-in-out;
}
.is-shady:hover {
  box-shadow: 0 10px 16px rgba(0, 0, 0, .13), 0 6px 6px rgba(0, 0, 0, .19);
}














































































































